
/* 
   Object instance data of object trajectory in the scene 
*/
export class EventObjectInstance {
 
    // Instance time [Epoch milliseconds Timestamp] 
    public timestamp: number;
 
    // Max X axis in the field-of-view of the bounding box 
    public maxX: number;
 
    // Max Y axis in the field-of-view of the bounding box 
    public maxY: number;
 
    // Min X axis in the field-of-view of the bounding box 
    public minX: number;
 
    // Min Y axis in the field-of-view of the bounding box 
    public minY: number;
 
    constructor(timestamp?: number, maxX?: number, maxY?: number, minX?: number, minY?: number) { 
        this.timestamp = timestamp;
        this.maxX = maxX;
        this.maxY = maxY;
        this.minX = minX;
        this.minY = minY;
    }

}


