import { UserTypeCode } from '../enums/UserTypeCode';
import { AccountRoleCode } from '../enums/AccountRoleCode';

/* 
   User registration data model - used by self registered users 
*/
export class UserRegistration {
 
    // User name 
    public name: string;
 
    // User email 
    public email: string;
 
    // User mobile phone number (for notification and validation) 
    public mobile: string;
 
    // Default account Id 
    public defaultAccount: string;
 
    // Account role: UNDEFINED | ADMIN | SUPERVISOR | OPERATOR | USER | INSTALLER 
    public role: AccountRoleCode;
 
    // User type: UNDEFINED | SYSADMIN | SUPPORT | USER 
    public type: UserTypeCode;
 
    // Temporary password (leave empty for system generated password) 
    public tempPassword: string;
 
    // User must change password on next login 
    public changePassword: boolean;
 
    // Flag to send email verification to the user 
    public verifyByEmail: boolean;
 
    constructor(name?: string, email?: string, mobile?: string, defaultAccount?: string, role?: AccountRoleCode, type?: UserTypeCode, tempPassword?: string, changePassword?: boolean, verifyByEmail?: boolean) { 
        this.name = name;
        this.email = email;
        this.mobile = mobile;
        this.defaultAccount = defaultAccount;
        this.role = role;
        this.type = type;
        this.tempPassword = tempPassword;
        this.changePassword = changePassword;
        this.verifyByEmail = verifyByEmail;
    }

}


