import { EventObjectInstance } from '../common/EventObjectInstance';

/* 
   Event object info
   Part of the event type to encapsulate all the info about the detected object (bounding box, trajectory etc) 
*/
export class EventObjectInfo {
 
    // Detected object id 
    public id: number;
 
    // Instance info of the object at the detection time (when the event was fired) 
    public detection: EventObjectInstance;
 
    // Trajectory (path) of all the object instances in the scene during the time it was in the scene 
    public trajectory: EventObjectInstance[];
 
    constructor(id?: number, detection?: EventObjectInstance, trajectory?: EventObjectInstance[]) { 
        this.id = id;
        this.detection = detection;
        this.trajectory = trajectory;
    }

}


