import { Coordinate } from '../common/Coordinate';

/* 
   Geo Reference data includes the contour of the raster image on the World coordinate system (WGS-84)
   and the list of transformation coefficients.
   To transform any point (x,y) on the FOV to WGS-84 coordinate, use the following functions:
   lng = (c0 * x) + (c1 * y) + c2
   lat = (c3 * y) + (c4 * x) + c5
   Where: c0..c5 are the indexed values of the coefficients vector e.g.
   c0 = coefficients[0], c1 = coefficients[1] ... c5 = coefficients[5]; 
*/
export class GeoReferenceData {
 
    // List of WGS-84 coordinates (contour of the FOV on map)<br> The order of the coordinates corresponds to the FOV corners: <ul> <li>coordinates[0] = ULC (Upper Left Corner)</li> <li>coordinates[1] = URC (Upper Right Corner)</li> <li>coordinates[2] = LRC (Lower Right Corner)</li> <li>coordinates[3] = LLC (Lower Left Corner)</li> </ul> 
    public coordinates: Coordinate[];
 
    // List of transformation coefficients matrix <br> Matrix has 9 values in the following order: m00, m01, m02, m10, m11, m12, m20, m21, m22 Use this matrix to convert any point (x,y) in the FOV to WGS-84 coordinate (lat, lng) <blockquote> <pre> [ lng ]     [ m<sub>00</sub>  m<sub>01</sub>  m<sub>02</sub> ]     [ x ] [ lat ]  =  [ m<sub>10</sub>  m<sub>11</sub>  m<sub>12</sub> ]  *  [ y ] [  w  ]     [ m<sub>20</sub>  m<sub>21</sub>  m<sub>22</sub> ]     [ 1 ] lng = (m00 * x) + (m01 * y) + m02 lat = (m10 * x) + (m11 * y) + m12 To convert back to non homogenous coordinates (X`,Y`): w = (m20 * x) + (m21 * y) + m22 X` = lng / w Y` = lat / w </pre> </blockquote> 
    public coefficients: number[];
 
    constructor(coordinates?: Coordinate[], coefficients?: number[]) { 
        this.coordinates = coordinates;
        this.coefficients = coefficients;
    }

}


