/* 
   Event status code 
*/
export enum EventStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // New event [1] 
    NEW = 1,
 
    // Open (in-progress) event [2] 
    OPEN = 2,
 
    // Closed event [3] 
    CLOSE = 3,
 
    // Irrelevant event [8] 
    IRRELEVANT = 8,
 
    // False event [9] 
    FALSE = 9,
 
}