import { Coordinate } from '../common/Coordinate';
import { Dimension } from '../common/Dimension';
import { GeoControlPoint } from '../common/GeoControlPoint';

/* 
   FOV (Sensor Field of View) Geo attributes 
*/
export class FovGeoAttributes {
 
    // Sensor FOV geo area polygon (the bounding polygon of the area covered by the FOV) 
    public geoArea: Coordinate[];
 
    // The normalized (1000 x 1000) visible area (rectangle) in the FOV 
    public visibleArea: Dimension;
 
    // Coefficient matrix for the FOV - WGS-84 affine transformation 
    public affineCoefficients: number[];
 
    // Min X axis in the field-of-view of the bounding box 
    public controlPoints: GeoControlPoint[];
 
    constructor(geoArea?: Coordinate[], visibleArea?: Dimension, affineCoefficients?: number[], controlPoints?: GeoControlPoint[]) { 
        this.geoArea = geoArea;
        this.visibleArea = visibleArea;
        this.affineCoefficients = affineCoefficients;
        this.controlPoints = controlPoints;
    }

}


