import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { ColorCode } from '../enums/ColorCode';
import { Coordinate } from '../common/Coordinate';
import { ObjectInstance } from '../common/ObjectInstance';

/* 
   Metadata object info - returned as search result 
*/
export class ObjectInfo {
 
    // Object tracking Id (This is not unique id across the system) 
    public objectId: string;
 
    // Sensor id 
    public sensorId: string;
 
    // Object type to search for: UNDEFINED | UNKNOWN | PERSON ... 
    public objectType: ObjectTypeCode;
 
    // Object color to search for: UNDEFINED | WHITE | BLACK ... 
    public color: ColorCode;
 
    // Object location in the world (WGS-84 coordinate system) 
    public location: Coordinate;
 
    // Instances of the object in the scene (trajectory) 
    public instances: ObjectInstance[];
 
    constructor(objectId?: string, sensorId?: string, objectType?: ObjectTypeCode, color?: ColorCode, location?: Coordinate, instances?: ObjectInstance[]) { 
        this.objectId = objectId;
        this.sensorId = sensorId;
        this.objectType = objectType;
        this.color = color;
        this.location = location;
        this.instances = instances;
    }

}


